@import "extras/variables";
@import "extras/typography";

@mixin textBig {
	font-size: 2.625rem;
	font-weight: 700;
}

@mixin textSmall {
	font-size: 1rem;
	font-weight: 300;
	line-height: 1.5rem;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	display: flex;
	height: 100vh;
	width: 100vw;
	background: #666666;
}

.card {
	position: relative;
	background: $color-background;
	margin: auto;
	width: 1440px;
	height: 960px;

	display: grid;
	grid-template-columns: repeat(2, auto);
	grid-template-rows: max-content auto;
	align-items: center;

	&__image {
		width: 505px;
		height: 330px;
		margin: 6rem 0 0 230px;
	}
}

.testimony {
	height: 100%;

	display: flex;
	flex-direction: column;

	margin-left: 6rem;
	margin-right: 230px;

	font-family: $font-main;
	color: $color-text;

	&__title {
		margin-top: 8.5rem;

		@include textBig();
	}

	&__text {
		margin-top: 1.5rem;
		margin-bottom: 2rem;

		@include textSmall;
	}

	&__btn {
		width: max-content;

		padding: 16px 40px;

		border: none;
		border-radius: 32px;

		font-family: inherit;
		font-size: 1rem;
		font-weight: 700;

		color: $color-secondary;
		background-color: $color-primary;

		cursor: pointer;
	}
}

.open {
	display: flex !important;
	opacity: 1 !important;
	// transform: translateY(0) !important;
}

.overlay {
	display: none;
	position: absolute;
	opacity: 0;
	transition: opacity 0.2s linear;
	top: 0;
	left: 0;
	z-index: 1;
	width: 100%;
	height: 100%;
	background: $color-dark;
	overflow: hidden;
}

.popup {
	display: none;
	justify-content: space-between;
	width: 920px;
	height: 210px;

	position: absolute;
	top: 50%;
	left: 50%;

	font-family: $font-main;
	color: black;
	background-color: $color-background;

	transform: translate(-50%, -50%);
	z-index: 2;

	&__content {
		display: flex;
		flex-direction: column;
		gap: 1.5rem;
		margin: auto 0 auto 6rem;
	}

	&__title {
		@include textBig();
	}

	&__text {
		@include textSmall;
	}

	&__btn {
		background-color: $color-secondary;
		border: none;
		width: 1.5rem;
		height: 1.5rem;
		margin: 2.5rem 2.5rem 0 0;
		@include textBig();
		cursor: pointer;

		&::before {
			content: "";
			position: absolute;
			height: 2px;
			width: 18px;
			background-color: $color-text;
			transform: translateX(-9px) rotate(45deg);
		}

		&::after {
			content: "";
			position: absolute;
			height: 2px;
			width: 18px;
			background-color: $color-text;
			transform: translateX(-9px) rotate(-45deg);
		}
	}
}
